import React from "react";
import imgWS from '../img/WS2020.jpg';


const Nosotros = () => {
  return (
    <>
      <div className="container mt-5">
        <h3>El patinaje artístico como disciplina deportiva en Uruguay y en el mundo.</h3>
        <br></br>
        <p>El patinaje artístico sobre ruedas es una demostración de rutinas de ballet coreografiadas al compás de la música, donde sobresalen los saltos, giros, trabajos de pie y elementos de danza. </p>
        <p>Se evalúa la velocidad, altura en los saltos, control, variedad, originalidad y el sentido de conexión de un elemento al otro.</p>

        <p>En Uruguay en general se patina con patines que tienen cuatro ruedas, dos de ellas en la punta y las otras dos en los talones.</p>

        <p>Los campeonatos son evaluados separados para la modalidad masculina y femenina, por categorias técnicas y finalmente por edad.</p>

        <p>Competidores internacionales y de alta liga compiten con dos partes: un programa corto (2:45+/-5) y otro largo (hombres: 4:30 +/- 10” y mujeres: 4:15 a 4:30). El último Panamericano en 2019 fue en la sede en Lima 2019.</p>

        <h4>Palabras claves</h4>

        <p>Preparación:</p>

        <p>Posición previa al cargamento del salto.</p>

        <p>Cargamento:</p>

        <p>Le sigue a la preparación y culmina con la máxima flexión de la pierna portante.</p>

        <p>Despegue:</p>

        <p>Es la fase en la que el deportista despega al salto.</p>

        <p>Vuelo:</p>

        <p>En esta fase el atleta tiene una posición estirada en el aire con los brazos cruzados al pecho y la pierna izquierda cruzada sobre la derecha.</p>

        <p>Aterrizaje:</p>

        <p>En esta fase el deportista cae sobre las ruedas del patín derecho con la pierna izquierda estirada adelante (a excepción del salto Euler).</p>

      </div>
      <div className="container mt-5">
        <h2>WORLD CHAMPIONSHIPS</h2><br /><br />
        <div>
          <button className="cart-btn">
            <a href="http://www.worldskate.org/artistic/news-artistic/3217-asuncion-2020-logo-presented-today-to-the-international-audience.html">
              <img src={imgWS} className="img" alt="" />
            </a>
          </button>
        </div>
      </div>

    </>
  );
};

export default Nosotros;
